import SocketService from './SocketService'


export default class NFTService {
    getTokenList(accountId) {
        const socket = new SocketService()
        return socket.getMainnet().get("/api/v1/tokens/", { params: {'account.id': accountId}}).then(res => res.data);
    }

    getNFTList(accountId, tokenId) {
        const socket = new SocketService()
        return socket.getMainnet().get(`/api/v1/tokens/${tokenId}/nfts`, { params: {'account.id': accountId}}).then(res => res.data);
    }
    
    getFTData(tokenId) {
        const socket = new SocketService()
        return socket.getMainnet().get(`/api/v1/tokens/${tokenId}`).then(res => res.data);
    }

    getMetadata(url) {
        const socket = new SocketService()
        return socket.getMainnet().get(url).then(res => res.data);
    }

    getGomintMetadata(tokenId) {
        const socket = new SocketService()
        return socket.getGomint().get("/query/verify.php", { params: {'network':'mainnet', 'tokenId':tokenId}}).then(res => {
            var pageContent = res.data
            if (pageContent.includes('"data": [')) {
                var metadataString = pageContent.split('"data": [')[1].split(']')[0]
                var metadata = JSON.parse(metadataString);
                return metadata
            } else {
                return {}
            }

        });
    }
 }